import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';

import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, Col } from 'antd';
import { TableData, ColEditDelete } from 'components/common';
import { getBonusNotes } from 'services/BNnote';
import FormFilterReward from './FormFilterReward';

class AddRewardPage extends Component {
  state = {};

  render() {
    const columns = [
      {
        title: 'Mã SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ tên',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'birthdate',
        key: 'birthdate',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Số QĐ',
        dataIndex: 'regNum',
        key: 'regNum',
      },
      {
        title: 'Ngày QĐ',
        dataIndex: 'decDay',
        key: 'decDay',
      },
      {
        title: 'Cấp khen thưởng',
        dataIndex: 'rewardLevel',
        key: 'rewardLevel',
      },
      {
        title: 'Hình thức',
        dataIndex: 'form',
        key: 'form',
      },
      {
        title: 'Loại khen thưởng',
        dataIndex: 'typeOfReward',
        key: 'typeOfReward',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#reward/updateForm/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    const { reload } = this.state;
    return (
      <Card title="Nhập khen thưởng">
        <Row>
          <Col span={24}>
            <FormFilterReward />
          </Col>
        </Row>
        <Row>
          <BtnAddNew href="#/reward/addForm" />
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              formSearch
              service={getBonusNotes}
              // paramSearch={paramSearchClass}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <AddRewardPage />
  </Page>
);



