import React, { Component } from 'react';
import { Col, Form, Input, Button } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import './style.scss';

@Form.create({ name: 'formTeacher' })
class FormFilterReward extends Component {
  state = {};
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <FilterPathEducate form={this.props.form} />
        <YearSemesterInput form={this.props.form} />
        <Col span={6}>
          <Form.Item label="Mã SV">{getFieldDecorator('email')(<Input />)}</Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item label="Họ tên">
            {getFieldDecorator('email', { initialValue: '' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item label="">
            <Button type="primary" htmlType="submit">
              Tìm kiếm
            </Button>
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormFilterReward;



