import { request } from 'utils';

request.prefix = '/api/students';

export const getBonusNotes = ({ label, page, pageSize = 10, orderBy, isPagingEnabled = true }) =>
  request({
    url: '/BonusNote',
    method: 'GET',
    params: { label, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getBonusNoteById = id =>
  request({
    url: `/BonusNote/${id}`,
    method: 'GET',
  });

export const deleteBonusNote = id =>
  request({
    url: `/BonusNote/${id}`,
    method: 'DELETE',
  });

export const updateBonusNote = ({ id, label, level }) =>
  request({
    url: `/BonusNote/${id}`,
    data: { id, label, level },
    method: 'PUT',
  });

export const createBonusNote = ({ label, level }) =>
  request({
    url: `/BonusNote`,
    data: { label, level },
    method: 'POST',
  });



